import React from "react";
import {Container, Row, Col} from 'react-bootstrap';
import { NetworkErrorMessage } from "./NetworkErrorMessage";

export function WalletStatus({ address, statusData, connectWallet, networkError, dismiss }) {
  let val = (
          <div>
          <p>Please connect to your wallet.</p>
          <button
            className="btn btn-warning"
            type="button"
            onClick={connectWallet}
          >
            Connect Wallet
          </button> 
          </div>
  );
  const blockNumber = statusData.currentBlock || 'loading';
  const cachedBlock = statusData.cachedBlock || 'loading';
  if(address){
    val = (<div>Connected Wallet: {address}</div>);
  }
  return (
    <div className="container">
      <div className="row justify-content-md-center">
        <div className="col-12 text-center">
        <Container>
        <Row>
          <Col>
            <div className="col-6 p-4 text-center">
              {val}
            </div>
          {/* Metamask network should be set to Localhost:8545. */}
          {networkError && (
            <NetworkErrorMessage 
              message={networkError} 
              dismiss={dismiss} 
            />
          )}
          </Col>
          <Col style={{fontSize:'15px'}}>
            Latest Block: {blockNumber}<br />
            Displayed Block: {cachedBlock}
          </Col>
        </Row>
        </Container>
        </div>
      </div>
    </div>
  );
}
