import React from "react";
import contractAddresses from "../contracts/addresses.json";
import categoriesToAttributes from "../categories.json"
// We'll use ethers to interact with the Ethereum network and our contract
import { ethers } from "ethers";
import {Navbar, Nav, NavDropdown, Form, FormControl, Button, Card, CardDeck} from 'react-bootstrap';
import {get_rgb} from "../helper.js";

export class AttributePage extends React.Component {
  constructor(props) {
    super(props);

    this.initialState = {
        canvasRendered : false,
        attrFilter: 'all'
    }

    this.state = this.initialState;
    this.init();
  }

  init(){
    for (var key of Object.keys(contractAddresses['Attribute'])) {
        this[`ref_${key}`] = React.createRef();
    }

  }

  componentDidMount() {
    this.drawCanvas();
  }

  componentDidUpdate() {
    this.drawCanvas();
  }

  drawCanvas() {
    if (!this.props.pixelData || this.state.canvasRendered)
        return;
    for (var key of Object.keys(contractAddresses['Attribute'])) {
        const canvas = this[`ref_${key}`].current;
        if (!!canvas)
        {
            const ctx = canvas.getContext("2d");
            let size = 16;
            let xFactor = ctx.canvas.width/16;
            let yFactor = ctx.canvas.height/16;
            let pixels = this.props.pixelData[key]['pixels'];
            let invert = false;
            if(key.includes('White'))
                invert = true;
            for (var i = 0;i < pixels.length; i++)
            {
                var y = parseInt(i/16) * yFactor;
                var x = (i % 16) * xFactor;
                let draw = false;
                if(this.props.pixelData[key]['useMap'][i])
                    draw = true;
                if(draw){
                    let rgb = get_rgb(pixels[i]);
                    ctx.fillStyle = `rgb(${rgb[0]}, ${rgb[1]}, ${rgb[2]})`;
                    ctx.fillRect(x,y, xFactor, yFactor);
                }
                else if(invert){
                    ctx.fillStyle = `rgb(0, 0, 0)`;
                    ctx.fillRect(x,y, xFactor, yFactor);
                }
            }
            this.setState({canvasRendered:true});
        }
    }
  }

  renderAttributeGrid() {

    const els = [];
    for (var key of Object.keys(contractAddresses['Attribute'])) {
        let remaining = 'loading';
        let initial = 'loading';
       var category;
       if (this.props.attrData){
            let attrData = this.props.attrData[key];
            if (attrData){
                remaining = attrData['remainingGenePool'];
                initial = attrData['initialGenePool'];
                category = attrData['category'];
            }
        }
        if (this.state.attrFilter==='all' || this.state.attrFilter===category){
            els.push(<Card key={key} style={{textAlign:'center', minWidth: "10rem", maxWidth: "10rem", borderColor:'#008080', marginBottom: '1%', marginRight:'1%'}}><Card.Body><Card.Title>{key.replace(/([A-Z])/g, ' $1')}</Card.Title><canvas width='128px' height='128px' style={{margin:'0', padding:'0', width:'100%'}} ref={this[`ref_${key}`]}/><Card.Text>{remaining}/{initial}</Card.Text></Card.Body></Card>);
        }
    }
        return (
            <CardDeck style={{display:'flex', flexWrap:'wrap'}}>
            {els}
            </CardDeck>
        );
    }

  onChangeFilter(e){
    this.setState({attrFilter:e.target.value})
    this.drawCanvas();
  }

  render() {
    return (
       <div style={{width:'80%', margin: '0 auto'}}>
       <div style={{marginLeft:'1%'}}>
        Attribute Filter {" "}
       <select onChange={this.onChangeFilter.bind(this)} style={{margin: '0 auto'}}>
        {['all'].concat(Object.keys(categoriesToAttributes)).map(num => <option value={num} key={num}>{num}</option>)}
        </select>
        </div>
        <br />
        {this.renderAttributeGrid()}
        </div>
    );
  }

}
