import React, {useState} from "react";
import { ethers } from "ethers";
import {Link} from "react-router-dom";
import "./Dapp.css";

export function RecruitMember({ recruitMember, isApproved, getApproval, pricing, defaultValue}) {
  const [approved, setApproved] = useState(false);
  const [state, setState] = useState({
      sAddr: defaultValue,
      sId: ""
  });
  
  function inputChanged(event){
      const val = event.target.value
      setState({
        ...state,
        [event.target.name]: val
      });
      let sAddr = state.sAddr;
      let sId = state.sId;
      if(event.target.name === 'sAddr')
          sAddr = val;
      else if(event.target.name === 'sId')
          sId = val;
      checkApproval(sAddr, sId);
  }

  function checkApproval(sAddr, sId){
      if(ethers.utils.isAddress(sAddr) && sId != ""){
          isApproved(sAddr, sId).then((result)=>{
            setApproved(result != ethers.constants.AddressZero);
          });
      }

  }

  function recruitMemberClicked(event){
      if (state.sAddr && state.sId) {
        recruitMember(state.sAddr, state.sId).then((result)=>{
      });
      }
    }
  function getApprovalClicked(event){
      if (state.sAddr && state.sId) {
        getApproval(state.sAddr, state.sId).then((result)=>{
            checkApproval(state.sAddr, state.sId);
        });
      }
    }

  return (
    <div style={{textAlign:'left', background:'white', width:'50%', padding:"1%"}} className="MainBodyElement">
      <h4>Recruit a New Bit Villain</h4>
      <form
      >
        <p>
        An NFT sacrifice is required to recruit a new bit villain. Even if a bit villain is sent to Valhalla, the sacrifice will remain locked up in perpetuity.
        </p>
        <p>
         In the testnet environment, there is a <Link to="/helper">sample NFT contract </Link>used to generate a new sacrificial NFT for recruitment. Once minted successfully, it appears in the "Sacrificial NFT Mints" table with the corresponding owner and generated sacrifice id.
        <br />
        <br />
        The sample NFT contract (and like all ERC721 standard NFTs on ethereum) first requires an approval transaction to allow the BV contract to transfer the sacrifice id to itself. After the approval transaction is confirmed, the sacrifice can be used to recruit a new bit villain.
        </p>
        <div className="form-group">
          <label>Sacrifice Contract address (pre-populated with sample NFT)</label>
          <input className="form-control" type="text" name="sAddr" required
            value={state.sAddr}
            onChange={inputChanged}
            disabled={true}
          />
        </div>
        <br />
        {approved &&
            <div>APPROVED</div>
        }
        {!approved &&
        <div className="form-group">
          <input className="btn btn-primary" type="button" value="GetApproval"
             onClick={getApprovalClicked}/>
        </div>
        }
        <br />
        <div className="form-group">
          <label>Sacrifice Id</label>
          <input className="form-control" type="number" name="sId" required onChange={inputChanged} value={state.sId}/>
        </div>
        {approved &&
        <div className="form-group">
          <input className="btn btn-primary" type="button" value="RecruitMember"
             onClick={recruitMemberClicked}/>
        </div>
        }
      </form>
    </div>
  );
}
