import React, {useState, useRef, useEffect} from "react";
import {useParams} from "react-router-dom";
import {draw_villain} from "../helper.js";
import './Dapp.css';

const IMG_SIZE = 384
export function Villain({villainData, pixelData, userAddress, tribulate, valhalla, pricing}) {
  let {vId} = useParams();
  const [canvasRendered, setCanvasRendered] = useState(false);
  const canvasRef = useRef();
  const villain = villainData[vId];

  function drawCanvas(){
    const canvas = canvasRef.current;
    if(canvas && !canvasRendered){
        draw_villain(canvas.getContext('2d'), villain, pixelData, canvas.getContext("2d").canvas.width/16);
        setCanvasRendered(true);
    }
  }

  useEffect(() =>{
    drawCanvas();
  });
  if(!villain){
    return(<div>
      <h4>Villain {vId} not found!</h4>
    </div>);
  }
  const attrs = [];
  for (const attr of villain['attrs']){
    let s = "";
    for (const w of attr['name'].split(/(?=[A-Z])/))
        s+=w+" ";
    attrs.push(<div key={s}>{s}</div>);
  }
  return (
    <div style={{width:IMG_SIZE*1.1, textAlign:'center'}} className="MainBodyElement">
      <h4>Villain {vId}</h4>
      <div><canvas height={IMG_SIZE} width={IMG_SIZE} ref={canvasRef} /></div>
      <div>Owner: {villain['owner']}</div>
      <div>Sacrifice Contract Addr: {villain['sacrificeAddr']}</div>
      <div>Sacrifice Id: {villain['sacrificeId']}</div>
      <div>{attrs}</div>
      <div>Times Blessed: {villain['numBlessed']}</div>
      <div>Times Punished: {villain['numPunished']}</div>
      {villain['owner'] === userAddress &&
          <div>
          <div>
          <input className="btn btn-primary" type="button" value="Tribulate"
             onClick={()=>tribulate(vId)}/>
        </div>
          <div>
          <input className="btn btn-primary" type="button" value={"Valhalla for "+pricing.valhalla+" ETH"}
             onClick={()=>valhalla(vId, 0)}/>
        </div>
        <br />
        </div>
      }
    </div>
  );
}
