import React, {useState} from "react";
import 'react-bootstrap-table-next/dist/react-bootstrap-table2.min.css';
import BootstrapTable from 'react-bootstrap-table-next';
import {ButtonGroup, ToggleButton} from 'react-bootstrap';
import './Events.css';

export function Events({data}) {
    const [radioValue, setRadioValue] = useState('recruits');
    const radios = [
        { name: 'Recruits', value: 'recruits' },
        { name: 'Valhalla', value: 'valhallas' },
        { name: 'Tribulations', value: 'tribulations' },
    ];
    const showData = data[radioValue];
    return (
        <div>
        <ButtonGroup className='EventsControlButton' toggle>
        {radios.map((radio, idx) => (
          <ToggleButton
            key={idx}
            type="radio"
            variant="secondary"
            name="radio"
            value={radio.value}
            checked={radioValue === radio.value}
            onChange={(e) => setRadioValue(e.currentTarget.value)}
          >
            {radio.name}
          </ToggleButton>
        ))}
        </ButtonGroup>
        <BootstrapTable classes="EventsTable" keyField='eventKey' data={ showData.rows } columns={ showData.columns } style={{tableLayout:'auto', color:'blue'}}/>
        </div>
    );
}
