import React from "react";
import BootstrapTable from 'react-bootstrap-table-next';

export function HelperNFT({ awardItem, address, data}) {
  const rowStyle = (row, rowIndex) => {
      let style = {fontWeight:'normal'};
      if (row["to"] == address){
          style['fontWeight'] = 'bold';
      }
      return style;
  };
  return (
    <div style={{margin:'0 auto', textAlign:'left', border:'2px solid #008080', background:'white', width:'50%', padding:"1%"}}>
      <h4>Generate a Sample Sacrificial NFT</h4>
      <form
      >
        <div className="form-group">
          <input className="btn btn-primary"
            value="Mint NFT" 
            type="button"
            onClick={(event) => {
              if (address) {
                awardItem(address);
              }
            }}
          />
        </div>
      </form>
      <br />
      <h4>Sacrificial NFT Mints</h4>
        <BootstrapTable classes="EventsTable" keyField='tokenId' data={ data.rows } columns={ data.columns } style={{tableLayout:'auto', color:'blue'}} rowStyle={rowStyle}/>
    </div>
  );
}
